<template>
  <div class="myHome">
    <div style="padding: 20px">
      <!--面包屑-->
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '' }">
          <span style="color: white">资源管理</span>
        </el-breadcrumb-item>
        <el-breadcrumb-item>
          <span style="color: white">待审核资源</span>

        </el-breadcrumb-item>
      </el-breadcrumb>

      <div>
        <div style="padding: 10px ;float: left">
          <el-input style="width: 200px" placeholder="请输入资源包名" suffix-icon="el-icon-search" class="ml-5"
                    v-model="packageName"></el-input>
        </div>
        <div style="padding: 10px ;float: left">
          <el-input style="width: 200px" placeholder="请输入上传者名称" suffix-icon="el-icon-search"
                    v-model="packageAuthor"></el-input>
        </div>
        <div style="padding: 10px ;float: left">
          <el-button style="margin-left: 10px" type="primary" @click="load">搜索</el-button>
        </div>

        <div style="padding: 10px ;float: left;">
          <el-button style="margin-left: 10px" type="success" @click="clearFilter">重置筛选条件</el-button>
        </div>
        <div style="padding: 10px ;float: left;">
          <el-button style="margin-left: 10px" type="primary" @click="load">刷新列表</el-button>
        </div>

      </div>
      <el-table :data="tableData"
                border stripe
                :header-cell-style="{background:'#42b983',color:'white'}"
                :stripe=true
                :highlight-current-row="true"
                style="font-size: 15px"
                max-height="600"
      >
        <el-table-column prop="pendingReviewPackageName" label="资源包名" min-width="20%" :resizable="false">
        </el-table-column>
        <el-table-column prop="packageCnName" label="资源中文名" min-width="15%" :resizable="false">
        </el-table-column>
        <el-table-column prop="pendingReviewPackageType" label="资源包类型" min-width="10%" :resizable="false">
        </el-table-column>
        <el-table-column prop="pendingReviewPackageAuthor" label="上传者" min-width="10%" :resizable="false">
        </el-table-column>
        <el-table-column prop="pendingReviewPackageTime" label="上传时间" min-width="15%" :resizable="false">
        </el-table-column>

        <!--      根据内容状态展示不一样的效果-->
        <el-table-column prop="pendingReviewPackageStatus" label="资源包状态" min-width="10%" :resizable="false">
          <template slot-scope="scope">
          <span v-if="scope.row.pendingReviewPackageStatus === '待审核'">
            <i class="el-icon-warning" style="color: red"></i>待审核
          </span>
            <span v-if="scope.row.pendingReviewPackageStatus === '待发布'">
            <i class="el-icon-success" style="color: #13ce66"></i>扫描完成，待发布
          </span>
            <span v-if="scope.row.pendingReviewPackageStatus === '已拒绝'">
            <i class="el-icon-remove" style="color: #8c939d"></i>审核拒绝
          </span>
            <span v-if="scope.row.pendingReviewPackageStatus === '已忽略'">
            <i class="el-icon-remove" style="color: #8c939d"></i>已忽略
          </span>
          </template>
        </el-table-column>
        <el-table-column prop="" label="操作" min-width="40%" align="center">
          <template v-slot="scope">
            <el-button type="info" size="mini"
                       @click="getPackageDetailInfoButton(scope.row.pendingReviewPackageId,scope.row.pendingReviewPackageName,scope.row.connectedDetailInfoId)">
              <i class="el-icon-view">查看详情</i>
            </el-button>
            <el-button type="warning" size="mini"
                       @click="checkPackageButton(scope.row.pendingReviewPackageId,scope.row.pendingReviewPackageName,scope.row.pendingReviewPackageStatus,scope.row.connectedDetailInfoId)">
              <i class="el-icon-question">安全检查</i>
            </el-button>
            <el-button type="danger" size="mini"
                       @click="refusePackageButton(scope.row.pendingReviewPackageId,scope.row.pendingReviewPackageName,scope.row.pendingReviewPackageStatus,scope.row.connectedDetailInfoId)">
              <i class="el-icon-errort">拒绝</i>
            </el-button>
            <el-button type="success" size="mini"
                       @click="publicPackageButton(scope.row.pendingReviewPackageId,scope.row.pendingReviewPackageName,scope.row.pendingReviewPackageStatus,scope.row.connectedDetailInfoId)">
              <i class="el-icon-s-promotion">发布</i>
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--各种操作的对话框-->
      <!--查看详情-->
      <el-dialog title="资源包基础信息" :visible.sync="packageDetailDialogFormVisible">
        <el-card>
          <div slot="header" style="text-align: left">
            <span>{{ this.packageNameInDialog }}</span>
          </div>
          <div style="text-align: left;color: black;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">资源包名:</span>
            <span style="font-weight: bold; padding-right: 10px;color: #1482f0 ;font-size: larger">{{this.packageDetailInfo.connectedPackageOriginalFileName}}</span>
          </div>
          <div style="text-align: left;color: black;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">资源包大小:</span>
            <span style="font-weight: bold; padding-right: 10px;color: #1482f0 ;font-size: larger">{{this.packageDetailInfo.connectedPackageSize}}</span>
          </div>
          <div style="text-align: left;color: black;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">服务中文名:</span>
            <span style="font-weight: bold; padding-right: 10px;color: #1482f0 ;font-size: larger">{{this.packageDetailInfo.softwareCnName}}</span>
          </div>
          <div style="text-align: left;color: black;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">运行环境:</span>
            <span style="font-weight: bold; padding-right: 10px;color: #1482f0 ;font-size: larger">{{this.packageDetailInfo.softwareEnvironment }}</span>
          </div>
          <div style="text-align: left;color: black;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">服务版本号:</span>
            <span style="font-weight: bold; padding-right: 10px;color: #1482f0 ;font-size: larger">{{this.packageDetailInfo.softwareVersion }}</span>
          </div>
          <div style="text-align: left;color: black;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">服务唯一标识:</span>
            <span style="font-weight: bold; padding-right: 10px;color: #1482f0 ;font-size: larger">{{this.packageDetailInfo.softwareUniqueName }}</span>
          </div>
          <div style="text-align: left;color: black;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">容器服务CPU需求(cpuRequests):</span>
            <span style="font-weight: bold; padding-right: 10px;color: #1482f0 ;font-size: larger">{{this.packageDetailInfo.cpuRequests}}</span>
          </div>
          <div style="text-align: left;color: black;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">容器服务内存需求(memoryRequests):</span>
            <span style="font-weight: bold; padding-right: 10px;color: #1482f0 ;font-size: larger">{{this.packageDetailInfo.memoryRequests }}</span>
          </div>

          <div style="text-align: left;color: black;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">服务开发者:</span>
            <span style="font-weight: bold; padding-right: 10px;color: #1482f0 ;font-size: larger">{{this.packageDetailInfo.developerName }}</span>
          </div>
          <div style="text-align: left;color: black;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">服务开发单位:</span>
            <span style="font-weight: bold; padding-right: 10px;color: #1482f0 ;font-size: larger">{{this.packageDetailInfo.developerGroup}}</span>
          </div>
          <div style="text-align: left;color: black;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">联系方式:</span>
            <span style="font-weight: bold; padding-right: 10px;color: #1482f0 ;font-size: larger">{{this.packageDetailInfo.developerPhone}}</span>
          </div>

        </el-card>

        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="packageDetailDialogFormVisible = false">确 定</el-button>
        </div>
      </el-dialog>

      <!--安全检查-->
      <el-dialog title="资源包安全扫描" :visible.sync="checkPackageDialogFormVisible" :close-on-click-modal="false"
                 :show-close="false">
        <el-card>
          <div slot="header" style="text-align: left">
            <span>{{ this.packageNameInDialog }}</span>
          </div>
          <div>扫描进度</div>
          <el-progress
            :text-inside="true"
            :stroke-width="26"
            :percentage="this.checkProgressValue"
            style="padding-left:10px; padding-right: 10px;padding-bottom: 20px;color: #eb194d"
            :color="customColors"
          >
          </el-progress>
          <div style="padding-top: 20px">扫描引擎版本：0.1</div>
        </el-card>
        <div slot="footer" class="dialog-footer">
          <el-button type="danger" @click="cancelCheckPackageButton">取 消</el-button>
          <el-button type="primary" @click="checkFinishButton" v-if="this.checkProgressValue === 100">完 成</el-button>
        </div>
      </el-dialog>
      <el-dialog
        title="中断扫描"
        :visible.sync="cancelCheckPackageDialogFormVisible"
        :close-on-click-modal="false"
        width="30%">
        <span>此操作将中断资源包扫描，系统不会记录已扫描的进度和信息。</span>
        <span slot="footer" class="dialog-footer">
    <el-button type="primary" @click="continueCheckPackageButton">继续扫描</el-button>
    <el-button type="danger" @click="yesCancelCheckPackageButton">终止扫描</el-button>
  </span>
      </el-dialog>

      <!--拒绝-->
      <el-dialog title="拒绝此资源包" :visible.sync="refusePackageDialogFormVisible">
        <el-card>
          <div slot="header" style="text-align: left">
            <span>{{ this.packageNameInDialog }}</span>
            <span>{{ this.packageIdInDialog }}</span>
          </div>
        </el-card>
        <span><i class="el-icon-warning"></i>拒绝资源包后，将无法对其再进行查看详情、安全检查、发布等操作，确认？</span>
        <div slot="footer" class="dialog-footer">
          <el-button type="info" @click="refusePackageDialogFormVisible = false">取 消</el-button>
          <el-button type="danger" @click="refusePackage">确 定</el-button>
        </div>
      </el-dialog>
      <!--发布-->
      <el-dialog title="发布资源包" :visible.sync="publishPackageDialogFormVisible">
        <el-card>
          <div slot="header" style="text-align: left">
            <span>{{ this.packageNameInDialog }}</span>
          </div>
        </el-card>
        <div slot="footer" class="dialog-footer">
          <el-button type="info" @click="publishPackageDialogFormVisible= false">取 消</el-button>
          <el-button type="primary" @click="publishPackage">确 定</el-button>
        </div>
      </el-dialog>

      <div style="padding: 10px 0;background-color: white">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="pageNum"
          :page-sizes="[2, 5, 10,20]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>

      </div>

    </div>
  </div>


</template>

<script>
import '../css/myCss.css'
export default {
  name: "PendingReviewPackage",
  data() {
    return {
      tableData: [],
      pageNum: 1,
      pageSize: 10,
      total: 0,

      packageName: "",
      packageAuthor: "",



      // 操作的时候，把当前用户名传回去，用作记录操作者日志
      user: window.sessionStorage.getItem('userAccount'),

      //  各个显示窗的状态
      // 当前对话框里的id,name，从scope里读出来，有用
      packageIdInDialog: '',
      packageNameInDialog: '',
      packageStatusInDialog: '',
      packageConnectedDetailId:'',

      // 详情
      // 对话框
      packageDetailDialogFormVisible: false,
      // 后端传回来的res.data
      packageDetailInfo:'',

      // 扫描
      checkPackageDialogFormVisible: false,
      checkProgressValue: 0,
      cancelCheckPackageDialogFormVisible: false,
      checkTimer: "",
      customColors: [
        {color: '#708090', percentage: 20},
        {color: '#f56c6c', percentage: 40},
        {color: '#e6a23c', percentage: 60},
        {color: '#1989fa', percentage: 80},
        {color: '#5cb87a', percentage: 100}
      ],
      // 拒绝
      refusePackageDialogFormVisible: false,

      // 发布
      publishPackageDialogFormVisible: false,


    }
  },
  created() {
    this.load();
  },
  destroyed() {
    clearInterval(this.checkTimer)
  },
  methods: {
    async load() {

      await this.request.get("/pendingReviewPackage/page", {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          packageName: this.packageName,
          packageAuthor: this.packageAuthor
        }
      }).then(res => {
        console.log(res)
        //tableData是前端声明的数据，records是mybatisPlus分页查询自定义的JSON里的参数，因此这边records前端会说它不认识
        this.tableData = res.records
        this.total = res.total
      })
      this.getPendingReviewPackageAsideNumber();
      this.getPublishedPackageAsideNumber();
      this.getRemovedPackageAsideNumber();
    },
    // 读取侧边栏数据
    getPendingReviewPackageAsideNumber() {
      this.request.get("/pendingReviewPackage/getPendingPackageAsideNumber").then(res => {
        this.$store.commit('setPendingPackageNumber', res.data);
      })
    },
    getPublishedPackageAsideNumber() {
      this.request.get("/publishedPackage/getPublishedPackageAsideNumber").then(res => {
        this.$store.commit('setPublishedPackageNumber', res.data);
      })
    },
    getRemovedPackageAsideNumber() {
      this.request.get("/removedPackage/getRemovedPackageAsideNumber").then(res => {
        this.$store.commit('setRemovedPackageNumber', res.data);
      })
    },

    handleSizeChange(pageSize) {
      console.log(pageSize)
      this.pageSize = pageSize
      this.load() //刷新数据
    },
    handleCurrentChange(pageNum) {
      console.log(pageNum)
      this.pageNum = pageNum
      this.load() //刷新数据
    },
    //清除搜索条件
    clearFilter() {
      this.packageName = "";
      this.packageAuthor = "";
      this.load();
    },


    // 最后一排的操作按钮
    // 查看详情
    // 注意这里加Button后缀的是操作按钮的单击方法，然后没有后缀的是和后端交互的方法

    // 查看详情，对话框和获取 详情写在一个方法里
    getPackageDetailInfoButton(id, name,detailId) {

      this.packageIdInDialog = id;
      this.packageNameInDialog = name;
      this.packageConnectedDetailId = detailId;
      this.request.post("/servicePackageDetailInfo/getServicePackageDetailInfoById", {
        params: {
          packageConnectedDetailId: this.packageConnectedDetailId
        }
      }).then( res => {
        if (res.code === '200') {
          this.packageDetailDialogFormVisible = true;
          this.packageDetailInfo = res.data;
        }
      })
    },
    // 安全检查
    checkPackageButton(id, name, status, detailId) {
      this.packageIdInDialog = id;
      this.packageNameInDialog = name;
      this.packageConnectedDetailId = detailId;
      if (status === "待审核") {
        // 打开窗口，启动定时器
        this.checkPackageDialogFormVisible = true;
        this.setCheckInterval();
      } else if (status === "已拒绝") {
        this.$message.error("审核拒绝的资源包，无法再进行安全扫描")
      } else if (status === "待发布") {
        this.$message.success("此资源包已经通过安全扫描了")
      }
    },
    // 写个假的进度条加速
    runCheckProgressValue() {
      if (this.checkProgressValue >= 100) {

      } else {
        let randomNumber = Math.round(Math.random() * 10);
        if (randomNumber + this.checkProgressValue >= 100) {
          this.checkProgressValue = 100;
        } else {
          this.checkProgressValue = this.checkProgressValue + randomNumber;
        }
      }
    },
    // 把进度条加速绑定在一个计时器上
    setCheckInterval() {
      this.checkTimer = setInterval(this.runCheckProgressValue, 200);
    },

    // 打开安全扫描取消窗口
    cancelCheckPackageButton() {
      this.cancelCheckPackageDialogFormVisible = true;
    },
    // 继续安全扫描
    continueCheckPackageButton() {
      this.cancelCheckPackageDialogFormVisible = false;
    },
    // 确认终止
    yesCancelCheckPackageButton() {
      this.cancelCheckPackageDialogFormVisible = false;
      this.checkPackageDialogFormVisible = false;
      this.checkProgressValue = 0;
      // 虽然每次启动都是新的set，但这里还是把计时器清掉吧
      clearInterval(this.checkTimer);
    },
    // 进度条读满这个按钮出现，发请求把数据库改了
    async checkFinishButton() {
      await this.request.post("/pendingReviewPackage/checkedById", {
        params: {
          packageId: this.packageIdInDialog,
          operator: this.user,
          servicePackageDetailInfoId: this.packageConnectedDetailId
        }
      }).then(res => {
        if (res.code === '200') {
          this.$message.success(res.msg)
        } else {
          this.$message.warning("系统异常，操作失败")
        }
      })
      // 重新Load刷新页面获取数据
      await this.load();
      this.checkPackageDialogFormVisible = false;
    },

    // 拒绝
    refusePackageButton(id, name, status, detailId) {
      this.packageIdInDialog = id;
      this.packageNameInDialog = name;
      this.packageConnectedDetailId = detailId;
      if (status === "已拒绝") {
        this.$message.error("这个资源包已经被拒绝过了")
      } else {
        this.refusePackageDialogFormVisible = true;
      }

    },
    async refusePackage() {
      await this.request.post("/pendingReviewPackage/refuseById", {
        params: {
          packageId: this.packageIdInDialog,
          operator: this.user,
          servicePackageDetailInfoId: this.packageConnectedDetailId
        }
      }).then(res => {
        if (res.code === '200') {
          this.$message.success(res.msg)
        } else {
          this.$message.warning("系统异常，操作失败")
        }
      })
      // 重新Load刷新页面获取数据
      await this.load();
      this.refusePackageDialogFormVisible = false;
    },
    // 发布
    publicPackageButton(id, name, status,detailId) {
      this.packageIdInDialog = id;
      this.packageNameInDialog = name;
      this.packageConnectedDetailId = detailId;
      if (status === "已拒绝") {
        this.$message.error("已拒绝的资源包无法进行发布。")
      } else if (status === "待审核") {
        this.$message.warning("资源包发布前请先进行安全检查。")
      } else if (status === "待发布") {
        this.publishPackageDialogFormVisible = true;
      }
    },
    async publishPackage() {
      await this.request.post("/pendingReviewPackage/publishById", {
        params: {
          packageId: this.packageIdInDialog,
          operator: this.user,
          servicePackageDetailInfoId: this.packageConnectedDetailId
        }
      }).then(res => {
        if (res.code === '200') {
          this.$message.success(res.msg)
        } else {
          this.$message.warning("系统异常，操作失败")
        }
      })
      // 重新Load刷新页面获取数据
      await this.load();
      this.publishPackageDialogFormVisible = false;
    },


  },
}
</script>

<style scoped>

</style>
